.amc-min-height{height: 516px;}
.amc-productblock {border-radius: 8px;border: 0px solid #FFF;background: #FFF;box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.12);padding: 16px 0px 40px 20px;width: 384px; height: 422px; border-radius: 8px; }
.amc-productblock .product-top-section{display: flex; align-items: end; justify-content: space-between; padding-bottom: 26px;}
.amc-productblock.hovered {top: -0.5rem;padding-top: 12px;position: relative;height: 516px;background: #9b9e51;color: #FFFFFF;border-radius: 0px;box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 40px 0 rgba(0, 0, 0, 0.19);}
.amc-productblock .product-top-section .left-side-data{width: 172px; height: 157px;}
.amc-productblock .product-top-section .right-side-img{width: 150px;height: 240px;}

