.multi-range{width: 100%;}
.multi-range .multi-range-slider {
    display: flex;
    position: relative;
    border: none; 
    border-radius: none;
    padding: 20px 0px;
   box-shadow: none;
    flex-direction: column;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
}
.multi-range-slider .bar .bar-left {padding: 0px !important;}
.multi-range .multi-range-slider .ruler,.multi-range .multi-range-slider .labels{display: none;}
.multi-range .multi-range-slider .bar::before{display: none;}
.multi-range .multi-range-slider .bar .bar-left{box-shadow: none;height: 4px;}
.multi-range .multi-range-slider .bar .bar-right{box-shadow: none;height: 4px;}
.multi-range .multi-range-slider .bar .bar-inner{box-shadow: none; border: none;}
.multi-range .multi-range-slider .thumb::before{box-shadow: none;top:4px; border:4px solid #9B9E51;height: 16px;width: 16px; margin: -11px -12px;}
/* multi-range-slider .thumb:active .caption */
.multi-range .multi-range-slider .thumb .caption *{background-color: #9B9E51;box-shadow: none;height: 20px; border-radius: 4px;display: flex; align-items: center;}
.multi-range .multi-range-slider .thumb .caption .max-caption{ font-size: 10px;font-weight: 700;line-height: 18px;letter-spacing: 0em;}
.multi-range .multi-range-slider .thumb .caption .max-caption::before,.multi-range .multi-range-slider .thumb .caption .min-caption::before{content: "";}
.multi-range .price-container{display: flex;justify-content: space-between;}
.multi-range .price-container .price-box{ position: relative;;display: flex;font-family: Poppins;font-size: 10px;font-weight: 400;line-height: 18px;letter-spacing: 0em;text-align: left;}
.multi-range .price-container .price-box .price-input{width: 90px;padding-left: 34px;}
.multi-range .price-container .price-box .price-title{position: absolute;top:11px;left:5px}
.multi-range .price-container .price-box .price-input:focus{border: 1px solid #CCC;}