.navcontainer{
    display: flex;
    padding-top: .8rem;
    justify-content:space-between;
    padding-right: 0!important;
    padding-left: 0!important;
    
}
.navcontainer .user {
    min-width: 200px;
    padding-right: 12px;
}
.logo{
    padding-left: 2px!important;
}
.lang-a>a{
    padding: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
}
.lang-a a:hover{
    color: #999e51 !important;
}
.navbar{
    padding: 0.5rem 0 !important;
}

.logo img{
    height: auto;
    width: 5rem;
    margin-left: 0;
}
.user{
display: flex;
justify-content: flex-end;
}
.nav-col{
    padding: 0!important;
}


